import { motion } from "framer-motion";
import React from "react";
import {Entry} from './Entry';
import './Section.css';

export const Section = ({data}) => {
return (
    <motion.section className="cv-section" id ={data.id}>
        <motion.header className="cv-section-title"
        whileHover={{transform: "translateX(-15px)"}}>
            {data.title}
        </motion.header>
        {data.entries.map(entry => <Entry entryInfo={entry} />)}
    </motion.section>
)
}